.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fade-in {
  animation: fadein 1s ;
  animation-fill-mode: forwards;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
  
}
.fade-out {
  animation: fadeout 2s ;
}
@keyframes fadeout {
  from {
    opacity: 1;
    height: 100%;
  }
  to {
    opacity: 0;
    height: 0;
  }
}
.error {
  position: absolute;
  top: 0;
  left: auto;
}
.link {
  text-decoration: none;
}

.wrapper {
  position: relative;
}
.sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 15rem;
  height: 100vh;
  background-color: #f9f9f9;
  transition: left 0.5s ease-in;
  z-index: 999;
  
}
.content {
  margin-left: 0;
  padding: 1rem;
  transition: margin-left 0.5s ease-in;

}
.filter {
  display: block;
}
.close-btn {
  display: none;
  position: absolute;
  top: 0;
  right: 1px;
  font-size: 1.45rem;
  padding: 0 10px 2px 10px;
}
.close-btn:hover {
  color: tomato;
}
@media (min-width: 768px) {
  .content {
    margin-left: 15rem;
    transition: margin-left 0.5s ease-in;
  }
  .filter {
    display: none;
  }
}

@media (max-width: 767px) {
  .sidebar {
    left: -15rem;
  }
  .sidebar.active {
    left: 0;
    transition: left 0.5s ease-in;
  }
  
  .close-btn {
    display: block;
    
  }
}


